// External imports, installed libraries, etc
import React, { useContext } from 'react';
import PropTypes from 'prop-types';

// Internal data, functions, custom hooks, etc
import { GenderContext } from '../genderContext/genderContext.js';

// Internal components, images, etc
import SpanButton from '../elements/SpanButton';


const ToggleGender = ({children, className, variant, marginbottom, component}) => {
	const { otherGenderData, currentGender, setCurrentGender } = useContext(GenderContext);

	const toggleGender = () => {
		if (currentGender === 1) {
			setCurrentGender(2);
			sessionStorage.setItem('localStateCurrentGender', 2);
		} else {
			setCurrentGender(1);
			sessionStorage.setItem('localStateCurrentGender', 1);
		}
		window.scrollTo(0,0);
	};

	return (
		<SpanButton onClick={toggleGender} className={className} variant={variant} marginbottom={marginbottom} component={component}>
			{children ? 
				(children) : 
				(otherGenderData.nameCap + "'s website")
			}
		</SpanButton>
	);
};

export default ToggleGender;


ToggleGender.defaultProps = {
	elementType: 'button',
	title: 'Toggle gender view',
};

ToggleGender.propTypes = {
	elementType: PropTypes.string,
	children: PropTypes.any,
	title: PropTypes.string,
};
