// External imports, installed libraries, etc
import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

// Internal data, functions, custom hooks, etc
import colors from '../../../styles/colors';
import breakpoints from '../../../styles/breakpoints';
import fonts from '../../../styles/fonts';

// Internal components, images, etc
import LinkStyle from '../Link/LinkStyle';

const ButtonStylesAsButton = ( props ) => {
	return (`
		border: 3px solid ${colors.primary[0]};
		background: ${props.background};
		color: ${props.color};
		display: ${props.display};
		font-family: ${fonts.bespoke.fontFamily};
		font-weight: ${fonts.bespoke.fontWeight.regular};
		padding: .9rem 1rem;
		text-align: center;
		text-decoration: none;
		text-transform: uppercase;
		transition: background-color .3s, border-color .3s, margin .3s;

		&:hover, &:focus {
			background: ${colors.primary.dark};
			border-color: ${colors.primary.dark};
			color: ${colors.white};
		}

		@media (min-width: ${breakpoints.mdUp}px) {
			display: ${props.display};
			min-width: 275px;
		}
	`);
}

const ButtonStyles = styled.span`
	${props => props.component === 'button' && (ButtonStylesAsButton(props))}
	${props => props.component === 'link' && (LinkStyle(props))}
`;

const Component = ( {...props} ) => {
	const color = props.variant === 'secondary' ? colors.primary[0] : colors.white;
	const background = props.variant === 'secondary' ? colors.white : colors.primary[0];
	const display = props.component === 'link' ? 'inline' : 'inline-block';

	return (
		<ButtonStyles {...props} color={color} background={background} display={display}>
			{props.children}
		</ButtonStyles>
	);
}

export default Component;

Component.defaultProps = {
	component: 'button',
};

Component.propTypes = {
	children: PropTypes.any,
	component: PropTypes.oneOf(['button', 'link']),
};
