// External imports, installed libraries, etc
import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

// Internal data, functions, custom hooks, etc

// Internal components, images, etc
import ButtonStyles from './ButtonStyles';

const Button = styled.button`
	display: inline-block;
	margin-bottom: ${props => props.marginbottom && props.marginbottom + 'rem'};
`;

const Component = ( {children, className, onClick, title, variant, component, marginbottom} ) => {
	return (
		<Button onClick={onClick} title={title} marginbottom={marginbottom}>
			<ButtonStyles className={className} variant={variant} component={component} >
				{children}
			</ButtonStyles>
		</Button>
	);
}

export default Component;

Component.defaultProps = {
	
};

Component.propTypes = {
	children: PropTypes.any,
};
