// External imports, installed libraries, etc
import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Link as _Link } from "react-router-dom";

// Internal data, functions, custom hooks, etc

// Internal components, images, etc
import ButtonStyles from '../Button/ButtonStyles';

const Link = styled(_Link)`
	display: inline-block;
	margin-bottom: ${props => props.marginbottom ? props.marginbottom + 'rem' : '10px'};
	text-decoration: none;
`;


const LinkButton = ( {children, className, onClick, title, to, variant, marginbottom} ) => {
	return (
		<Link to={to} title={title} onClick={onClick} marginbottom={marginbottom} >
			<ButtonStyles className={className} variant={variant}>
				{children}
			</ButtonStyles>
		</Link>
	);
}

export default LinkButton;

LinkButton.defaultProps = {
	to: '#',
};

LinkButton.propTypes = {
	to: PropTypes.string.isRequired,
	title: PropTypes.string,
	children: PropTypes.any,
	marginbottom: PropTypes.number,
};


