// External imports, installed libraries, etc
import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

// Internal data, functions, custom hooks, etc

// Internal components, images, etc
import ButtonStyles from '../Button/ButtonStyles';

const SpanButton = styled.span`
	cursor: pointer;
	display: inline-block;
	margin-bottom: ${props => props.marginbottom + 'rem'};
`;

const Component = ( {children, className, onClick, title, variant, component, marginbottom} ) => {
	return (
		<SpanButton onClick={onClick} title={title} marginbottom={marginbottom} role='button' tabIndex={0}>
			<ButtonStyles className={className} variant={variant} component={component}>
				{children}
			</ButtonStyles>
		</SpanButton>
	);
}

export default Component;

Component.defaultProps = {
	
};

Component.propTypes = {
	children: PropTypes.any,
};
